
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /*margin-top: 60px;*/
  background-color:#ccc;
}

/* site-custom-pagination */
.site li.swiper-pagination-customs{
    width:80px;
    height:34px;
    line-height:34px;
    border-radius:18px;
    background-color:rgba(255,255,255,0);
    cursor:pointer;
    -webkit-transition: all .25s;
    transition: all .25s;
}
.site li:hover{
    color:#00c0ff;
}
.site li.active:hover{
    color:#fff;
}
.site li.active{
    background-color:#00c0ff;
    color:#fff;
    /*background-color:#F78E00;*/
}

/* banner-custom-pagination */
.banner-pagination li.banner-customs{
  width:45px;
  height:20px;
  border-top:4px solid #fff;
  background-color: rgba(255,255,255,0);
  cursor:pointer;
}
.banner-pagination li.active{
  border-top-color:#00c0ff;
  /*background-color:#00c0ff;*/
}

/*side-open*/
.side-open{
    position:fixed;
    right:40px;
    top:25px;
    width:40px;
    height:30px;
    cursor:pointer;
    background:rgba(255,255,255,0);
    -webkit-transition: 0.4s cubic-bezier(0.215,0.61,0.355,1);
    transition: 0.4s cubic-bezier(0.215,0.61,0.355,1);
    z-index:100;
}
.side-open hr{
    position:absolute;
    height:4px;
    background-color:#00c0ff;
    display:block;
    margin:0;
    padding:0;
    border:none;
    -webkit-transition:all .5s;
    transition:all .5s;
}
.side-open hr:nth-child(1){
    width:100%;
    top:0;
    right:0;
}
.side-open hr:nth-child(2){
    width:40%;
    top:50%;
    left:0;
    margin-top:-2px;
    opacity:1;
}
.side-open hr:nth-child(3){
    width:40%;
    top:50%;
    right:0;
    margin-top:-2px;
    opacity:1;
}
.side-open hr:nth-child(4){
    width:100%;
    bottom:0;
    right:0;
}

